// --- Office Add-in logic ---
Office.onReady(() => {
  document.getElementById('fetchUserBtn').addEventListener('click', fetchAndInsertData);
});

async function fetchAndInsertData() {
  const statusDiv = document.getElementById('status');
  statusDiv.textContent = '';
  const projectId = document.getElementById('projectIdInput').value.trim();
  if (!projectId) {
    statusDiv.textContent = "Please enter a Project ID.";
    return;
  }

  try {
    // Fetch project from Supabase
    const { data, error } = await supabase
      .from('projects')
      .select('*')
      .eq('id', projectId)
      .single();

    if (error || !data) throw new Error("No project found or fetch error");

    // Insert data into Word content controls
    await Word.run(async (context) => {
      const contentControls = context.document.contentControls;
      contentControls.load("items/tag");
      await context.sync();

      contentControls.items.forEach((cc) => {
        if (cc.tag === "projectName") {
          cc.insertText(data.projectName || "", Word.InsertLocation.replace);
        } else if (cc.tag === "clientName") {
          cc.insertText(data.clientName || "", Word.InsertLocation.replace);
        } else if (cc.tag === "projectAddress") {
          cc.insertText(data.projectAddress || "", Word.InsertLocation.replace);
        } else if (cc.tag === "clientAddress") {
          cc.insertText(data.clientAddress || "", Word.InsertLocation.replace);
        } else if (cc.tag === "deadline") {
          cc.insertText(data.deadline || "", Word.InsertLocation.replace);
        } else if (cc.tag === "projectId") {
          cc.insertText(data.id || "", Word.InsertLocation.replace);
        }
      });

      await context.sync();
    });
    statusDiv.textContent = "Project data loaded successfully!";
    statusDiv.style.color = "green";
  } catch (error) {
    console.error("Supabase fetch failed:", error);
    statusDiv.textContent = "Failed to fetch project data from Supabase.";
    statusDiv.style.color = "red";
    await Word.run(async (context) => {
      context.document.body.insertText('Failed to fetch project data from Supabase.', Word.InsertLocation.end);
      await context.sync();
    });
  }
}
